"use client"

import Link from "next/link"
import { usePathname } from "next/navigation"
import { Home, Drama, User, MessageSquare, X, LogOut, Zap } from "lucide-react"
import UserPlanBadge from "@/components/UserPlanBadge"
import { getMockUserUsage } from "@/lib/usage-calculator"

interface AppSidebarProps {
  isOpen: boolean
  onClose: () => void
}

export default function AppSidebar({ isOpen, onClose }: AppSidebarProps) {
  const pathname = usePathname()
  const userUsage = getMockUserUsage()

  const navItems = [
    { href: "/home", icon: Home, label: "Home" },
    { href: "/expat-ai", icon: MessageSquare, label: "Expat AI Chat Bot" },
    { href: "/role-play", icon: Drama, label: "Cultural Role-Play" },
    { href: "/profile", icon: User, label: "Profile" },
  ]

  return (
    <>
      {/* Mobile backdrop */}
      {isOpen && (
        <div
          className="fixed inset-0 bg-black/20 backdrop-blur-sm z-40 md:hidden"
          onClick={onClose}
        />
      )}

      {/* Sidebar */}
      <aside
        className={`
          fixed md:static inset-y-0 left-0 z-50
          ${isOpen ? "w-64" : "w-0 md:w-64"}
          bg-white border-r border-gray-200 flex flex-col justify-between
          transition-all duration-300 ease-in-out overflow-hidden
        `}
      >
        {/* Header */}
        <div className="p-6 border-b border-gray-200 flex items-center justify-between">
          <Link href="/home" className="flex items-center gap-2">
            <img src="/logo.jpeg" alt="WorkAbroadly" className="h-8 w-auto" />
            <span className="font-semibold text-lg text-gray-800">WorkAbroadly</span>
          </Link>
          <button
            onClick={onClose}
            className="md:hidden p-2 rounded-full hover:bg-gray-100 transition-colors text-gray-700"
          >
            <X className="w-5 h-5" />
          </button>
        </div>

        {/* Navigation */}
        <nav className="flex-1 p-6 space-y-2 overflow-y-auto">
          {navItems.map((item) => {
            const Icon = item.icon
            const isActive = pathname === item.href
            return (
              <Link
                key={item.href}
                href={item.href}
                className={`flex items-center gap-3 rounded-full px-4 py-3 font-medium transition-colors ${
                  isActive
                    ? "bg-black text-white"
                    : "hover:bg-gray-100 text-gray-700"
                }`}
              >
                <Icon className="w-5 h-5" />
                <span>{item.label}</span>
              </Link>
            )
          })}
        </nav>

        {/* Plan section + Logout */}
        <div className="p-6 border-t border-gray-200 space-y-4">
          <div className="p-4 border rounded-2xl bg-gray-50">
            <div className="flex items-center gap-2 mb-2">
              <Zap className="w-5 h-5 text-gray-700" />
              <span className="font-semibold text-gray-800">Professional</span>
            </div>
            <p className="text-sm text-gray-700">Credits Used: <b>{userUsage.used}</b></p>
            <p className="text-sm text-gray-700">Remaining Credits: <b>{userUsage.remaining}</b></p>
            <p className="text-sm text-gray-700 mt-1">Chat Sessions: —</p>
            <p className="text-sm text-gray-700">Role-Plays: —</p>
          </div>

          <Link
            href="/my-plan"
            className="block text-center text-sm text-gray-600 hover:text-gray-900 font-medium"
          >
            Manage Plan
          </Link>

          <Link
            href="/logout"
            className="flex items-center gap-3 rounded-full px-4 py-3 font-medium transition-colors hover:bg-red-50 text-red-600"
          >
            <LogOut className="w-5 h-5" />
            <span>Log Out</span>
          </Link>
        </div>
      </aside>

      {/* Bottom Nav for Mobile */}
      <nav className="md:hidden fixed bottom-0 left-0 right-0 bg-white border-t border-gray-200 shadow-lg z-30">
        <div className="flex items-center justify-around py-3">
          {navItems.map((item) => {
            const Icon = item.icon
            const isActive = pathname === item.href
            return (
              <Link
                key={item.href}
                href={item.href}
                className="flex flex-col items-center gap-1"
              >
                <Icon
                  className={`w-5 h-5 ${
                    isActive ? "text-gray-900" : "text-gray-600"
                  }`}
                />
                <span
                  className={`text-xs ${
                    isActive ? "font-medium text-gray-900" : "text-gray-600"
                  }`}
                >
                  {item.label.split(" ")[0]}
                </span>
              </Link>
            )
          })}
        </div>
      </nav>
    </>
  )
}
